// src/layouts/tables/residentsTableData.js

function residentsTableData(residents = []) {
  const columns = [
    { Header: "ID", accessor: "id", align: "left" },
    { Header: "Username", accessor: "username", align: "left" },
    // { Header: "Name", accessor: "name", align: "left" },
    { Header: "Villa No", accessor: "villa_number", align: "left" },
    { Header: "Email", accessor: "email", align: "left" },
    { Header: "Phone", accessor: "phone", align: "left" },
    { Header: "Registered", accessor: "registration_date", align: "left" },
  ];

  const rows = residents.map((res) => ({
    id: res.id,
    username: res.username,
    name: res.name || "N/A",
    email: res.email || "N/A",
    villa_number: res.villa_number,
    phone: res.phone,
    registration_date: res.registration_date,
  }));

  return { columns, rows };
}

export default residentsTableData;
