import MDTypography from "components/MDTypography";
import MDBox from "components/MDBox";

export default function maintenanceTableData(data) {
  const columns = [
    { Header: "Username", accessor: "username", align: "left" },
    { Header: "Amount", accessor: "amount", align: "center" },
    { Header: "Due", accessor: "due", align: "center" },
    { Header: "Month", accessor: "month", align: "center" },
    { Header: "Year", accessor: "year", align: "center" },
    { Header: "Status", accessor: "status", align: "center" },
    { Header: "Payment Method", accessor: "payment_method", align: "center" },
    { Header: "Payment Date", accessor: "payment_date", align: "center" },
  ];

  const rows = data.map((item) => {
    const isDue = item.due && parseFloat(item.due) > 0;
    const rowColor = isDue ? "error" : "success";

    return {
      username: (
        <MDBox>
          <MDTypography variant="button" color={rowColor}>
            {item.username}
          </MDTypography>
        </MDBox>
      ),
      amount: (
        <MDTypography variant="button" color={rowColor}>
          ₹{item.amount}
        </MDTypography>
      ),
      due: (
        <MDTypography variant="button" color={rowColor}>
          {item.due ? `₹${item.due}` : "—"}
        </MDTypography>
      ),
      month: (
        <MDTypography variant="button" color={rowColor}>
          {item.month}
        </MDTypography>
      ),
      year: (
        <MDTypography variant="button" color={rowColor}>
          {item.year}
        </MDTypography>
      ),
      status: (
        <MDTypography variant="button" color={rowColor}>
          {item.status}
        </MDTypography>
      ),
      payment_method: (
        <MDTypography variant="button" color={rowColor}>
          {item.payment_method || "—"}
        </MDTypography>
      ),
      payment_date: (
        <MDTypography variant="button" color={rowColor}>
          {item.payment_date}
        </MDTypography>
      ),
    };
  });

  return { columns, rows };
}
